.card {
    width: var(--card-width);
    height: var(--card-height);
    margin: 0.6rem;
    display: flex;
    flex-direction: column;
}

.card-img {
    flex: 1;
}

.card-img img {
    flex: 1;
    height: 80%;
}

.card-text {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card:hover {
    cursor: pointer;
    background-color: white;
}

@media (max-width: 600px) {
    .card {
        width: 100%;
        height: 150px;
        flex-direction: row;
    }
}