.order-wrapper {
    display: flex;
    min-height: calc(100vh - 50px);
    height: fit-content;
    margin-bottom: 1rem;
}

.order-products {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;   
}

.order-product {
    width: 250px;
    margin: 1rem !important;
}

@media (max-width: 600px) {
    .order-wrapper {
        flex-direction: column;
    }

    .order-categories {
        display: flex;
        justify-content: space-evenly;
        padding: 0;
    }

    .order-product {
        width: 150px;
    }
}