.choose {
    background-color: #003080;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.choose-wrapper {
    flex: 1;
    overflow: auto;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: white;
}

.choose-cards {
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: center;
}

.choose-wrapper h1 {
    font-size: 1.9rem;
}

@media (max-width: 600px) {
    .choose-cards {
        flex-direction: column;
    }
}